.addNewContentWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 20px;
    padding: 20px;
    border-radius: 20px;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.1), 0 5px 15px rgba(0, 0, 0, 0.2);
}

.addNewContentWrapper textarea{
    width: 100%;
    height: 60px;
    outline: none;
    resize: none;
    border-radius: 10px;
    border: 0;
    padding: 5px 20px;
    box-shadow: 0 1px 2px 0 rgba(29, 33, 38, 0.10), 0 5px 20px 0 rgba(29, 33, 38, 0.03);
    font-size: 19px;
}

